.btnBlock {
  display: block;
  width: 100%;
}
.componentsDescList {
  margin-bottom: 24px;
}
.componentsDescList .term {
  color: rgba(0, 0, 0, 0.85);
  display: table-cell;
  line-height: 20px;
  white-space: nowrap;
  padding-bottom: 16px;
  margin-right: 8px;
}
.componentsDescList .term::after {
  content: ":";
  margin: 0 8px 0 2px;
  position: relative;
  top: -0.5px;
}
.componentsDescList .detail {
  color: rgba(0, 0, 0, 0.65);
  padding-bottom: 16px;
  display: table-cell;
  line-height: 20px;
}



