.noticeIndexPopoverInnerContent .ant-popover-inner-content {
  padding: 0;
}
.noticeIndexPopoverInnerContent .ant-list-item {
  padding-left: 24px;
  padding-right: 24px;
}
.componentsNoticeIndexNoticeListClear {
  cursor: pointer;
  text-align: center;
}
.header {
  padding: 0;
  background-color: #fff;
  -webkit-box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
          box-shadow: 0 1px 4px rgba(0, 21, 41, 0.08);
}
.header .trigger {
  font-size: 18px;
  line-height: 64px;
  padding: 0 24px;
  cursor: pointer;
  -webkit-transition: color .3s;
  -o-transition: color .3s;
  transition: color .3s;
}
.header .globalHeaderIndexHeader {
  height: 64px;
  padding: 0 12px 0 0;
  background-color: #fff;
  position: relative;
}
.header .globalHeaderIndexRight {
  float: right;
  line-height: 64px;
}
.header .globalHeaderIndexAction {
  padding: 0 12px;
  height: 100%;
  cursor: pointer;
  display: inline-block;
  -webkit-transition: all .3s;
  -o-transition: all .3s;
  transition: all .3s;
}
.header .globalHeaderIndexAction:hover {
  background: #e6f7ff;
}
.header .globalHeaderIndexAction > i {
  font-size: 18px;
  color: rgba(0, 0, 0, 0.65);
  vertical-align: middle;
}
.header .globalHeaderIndexAvatar {
  margin: 20px 8px 20px 0;
  vertical-align: middle;
  color: #1890ff;
}



